const valueExists = /.*[^ ].*/
const onlyLettersAndWhitespace = /^(?! *$)[a-zA-Z.+ '-]+$/
const onlyNumbers = /^\d+$/
const validateEmail = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

export {
	valueExists,
	onlyLettersAndWhitespace,
	onlyNumbers,
	validateEmail
}